.layout {
    padding-top: 40px;
    padding-bottom: 10px;
    @media screen and (min-width: 1200px) {
      display: grid;
      gap: 40px;
      grid-template-areas: 'sidebar content';
    }
  }

  .books__item {
    @media screen and (max-width: 768px) {
      margin-bottom: 40px;
    }
  }
  .home__category-container {
    display: none;
  }
  .home__category-container.container_active {
    display: block;
  }
// .home-typeBook{ // kategpria do usuniecia - techniczna
//   display: block;
//   // background-color:yellowgreen;
//   // font-size: 25px;
// }
  .home-typeBook.container_active {
    display: block;
      }
  
  .category-item {
    color: rgba(17, 17, 17, 0.6);
    cursor: pointer;
    transform: scale(0.97);
    transform-origin: center left;
      }
  
  .category-item:not(:last-child) {
    margin-bottom: 24px;
    @media screen and (min-width: 768px) {
      margin-bottom: 32px;
    }
  
    @media screen and (min-width: 1200px) {
      margin-bottom: 24px;
    }
  }
  
  .category-item.active,
  .category-item:hover,
  .category-item:focus {
    text-transform: uppercase;
    font-weight: 700;
    color: #4f2ee8;
    transform: scale(1);
  }
  .books__card-author {
    @media screen and (min-width: 768px) {
      max-width: 218px;
    }
    @media screen and (min-width: 1200px) {
      max-width: 180px;
    }
  }
  .category__item {
    @media screen and (min-width: 1200px) {
      flex-basis: calc((100% - 100px) / 5);
    }
  }